// Mock API
const simulateLatency = (data: any) => new Promise(resolve => setTimeout(() => resolve(data), 500));

export const getQuestions = () => simulateLatency(questions.sort((a,b) => b.createdAt.getTime() - a.createdAt.getTime()));

export const getQuestionById = (id: string) => simulateLatency(questions.find(q => q.id === id));

export const addQuestion = (question: Omit<Question, 'id' | 'createdAt' | 'answers'>) => {
  const newQuestion: Question = {
    ...question,
    id: (questions.length + 1).toString(),
    createdAt: new Date(),
    answers: [],
  };
  questions = [newQuestion, ...questions];
  return simulateLatency(newQuestion);
};

export const addAnswer = (questionId: string, answer: Omit<Answer, 'id' | 'createdAt'>) => {
  const newAnswer: Answer = {
    ...answer,
    id: `a${Math.random()}`,
    createdAt: new Date(),
  };
  const question = questions.find(q => q.id === questionId);
  if (question) {
    question.answers.push(newAnswer);
  }
  return simulateLatency(newAnswer);
};

export const getLeaderboard = () => {
    const sortedUsers = [...users].sort((a, b) => b.score - a.score);
    return simulateLatency(sortedUsers);
};
